import { pushTarget, popTarget } from "./dep";
import { queueWatcher } from "./scheduler";

let id = 0; // åšä¸€ä¸ªwatcher çš„id æ¯æ¬¡åˆ›å»ºwatcheræ—¶ éƒ½æœ‰ä¸€ä¸ªåºå· 
// ç›®å‰å†™åˆ°è¿™é‡Œ åªæœ‰ä¸€ä¸ªwatcher æ¸²æŸ“watchrerï¼Œåªè¦è§†å›¾ä¸­ä½¿ç”¨åˆ°äº†è¿™ä¸ªå±æ€§ï¼Œè€Œä¸”å±æ€§å˜åŒ–äº†å°±è¦æ›´æ–°è§†å›¾

class Watcher {
    constructor(vm, exprOrFn, cb, options) {
        this.vm = vm;
        this.exprOrFn = exprOrFn;
        this.cb = cb;
        this.options = options;
        this.deps = []; // è¿™ä¸ªwatcherä¼šå­˜æ”¾æ‰€æœ‰çš„dep
        this.depsId = new Set();
        if (typeof exprOrFn == 'function') {
            this.getter = exprOrFn;
        }

        this.id = id++;
        this.get();
    }
    run(){
        this.get(); // é‡æ–°æ¸²æŸ“
    }
    get() {
        // 1.æ˜¯å…ˆæŠŠæ¸²æŸ“watcher æ”¾åˆ°äº† Dep.targetä¸Š
        // 2.this.getter()  æ˜¯ä¸æ˜¯å»é¡µé¢å–å€¼æ¸²æŸ“  å°±æ˜¯è°ƒç”¨definePropertyçš„å–å€¼æ“ä½œ
        // 3.æˆ‘å°±è·å–å½“å‰å…¨å±€çš„Dep.target,æ¯ä¸ªå±æ€§éƒ½æœ‰ä¸€ä¸ªdep å–å€¼æ˜¯å°±å°†Dep.target ä¿ç•™åˆ°å½“å‰çš„depä¸­
        // 4.æ•°æ®å˜åŒ– é€šçŸ¥watcher æ›´æ–° 

        pushTarget(this); // åœ¨å–å€¼ä¹‹å‰ å°†watcherå…ˆä¿å­˜èµ·æ¥
        this.getter(); // è¿™å¥è¯å°±å®ç°äº†è§†å›¾çš„æ¸²æŸ“  -ã€‹ æ“ä½œæ˜¯å–å€¼ 
        popTarget(); // åˆ æ‰watcher

        // Vueæ˜¯ç»„ä»¶çº§åˆ«æ›´æ–°çš„
    }
    addDep(dep) {
        let id = dep.id;
        if (!this.depsId.has(id)) {
            this.depsId.add(id);
            this.deps.push(dep);
            dep.addSub(this); // è®©å½“å‰dep è®¢é˜…è¿™ä¸ªwatcher
        }
    } 
    update(){ // æ›´æ–°åŸç†
        queueWatcher(this); // å°†watcherå­˜å‚¨èµ·æ¥
        // this.get();  // ä»¥å‰è°ƒç”¨getæ–¹æ³•æ˜¯ç›´æ¥æ›´æ–°è§†å›¾
    }
}


